html, body, body > div {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;

  background: #06beb6;
  background: -webkit-linear-gradient(to right, #48b1bf, #06beb6);
  background: -webkit-gradient(linear, left top, right top, from(#48b1bf), to(#06beb6));
  background: -webkit-linear-gradient(left, #48b1bf, #06beb6);
  background: -o-linear-gradient(left, #48b1bf, #06beb6);
  background: linear-gradient(to right, #48b1bf, #06beb6);
  color: #f1f1f1;
  font-family: 'Raleway', sans-serif;
}



