@-webkit-keyframes hide {
  0% { opacity: 1; }
  90% { opacity: 0; }
  100% { position: absolute; left: -500vw; }
}

@keyframes hide {
  0% { opacity: 1; }
  90% { opacity: 0; }
  100% { position: absolute; left: -500vw; }
}

@-webkit-keyframes show {
  0% { position: static; opacity: 0; }
  100% { opacity: 1; }
}

@keyframes show {
  0% { position: static; opacity: 0; }
  100% { opacity: 1; }
}

.hide {
  -webkit-animation: hide .25s ease-in-out forwards;
          animation: hide .25s ease-in-out forwards;
}

.show {
  -webkit-animation: show .25s ease-in-out forwards;
          animation: show .25s ease-in-out forwards;
}

.textCenter {
  text-align: center;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .hide {
    display: none !important;
  }

  .show {
    display: block !important;
  }
}

@supports (-ms-ime-align: auto) {
  .hide {
    display: none !important;
  }

  .show {
    display: block !important;
  }
}



